import { Component } from '@angular/core';
import {  FormGroup , FormControl } from '@angular/forms';
@Component({
  selector: 'app-nestedgroup',
  templateUrl: './nestedgroup.component.html',
  styleUrls: ['./nestedgroup.component.css']
})
export class NestedgroupComponent {
  formSignUp: FormGroup;
  
  constructor() { 
    this.formSignUp = new FormGroup({
      emailuser: new FormControl('linh@gmail.com'),
      passworduser: new FormControl(),
      subjects: new FormGroup({
        nodejs: new FormControl(false),
        angular4: new FormControl(),
        reactjs: new FormControl()
      })
    });
  }

  getSubmitReact(){
    console.log(this.formSignUp.value);
  }

}



