import { Component, OnInit } from '@angular/core';
import {  FormGroup , FormControl, FormBuilder, Validators } from '@angular/forms';
@Component({
  selector: 'app-formbuider',
  templateUrl: './formbuider.component.html',
  styleUrls: ['./formbuider.component.css']
})
export class FormbuiderComponent implements OnInit  {

  rfContact: FormGroup;
  constructor(private fb: FormBuilder) { }

  ngOnInit() {
    this.rfContact = this.fb.group({
      contactName: ['', [Validators.required, Validators.minLength(3)]],
      email: ['',[Validators.email, gmailValidator]],
      social: this.fb.group({
        facebook: ['', [Validators.required, Validators.minLength(3)]],
        twitter: '',
        website: ''
      }),
      tel: ''
    });
  }

  onSubmit() {
    // Do something awesome
    console.log(this.rfContact.value);
  }

}
 function gmailValidator(formComtrol: FormControl){
  if (formComtrol.value.includes('@gmail.com'))
  {
    return null;

  }
  else{console.log("please input gmail")}
  
 }



