import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-bai18',
  templateUrl: './bai18.component.html',
  styleUrls: ['./bai18.component.css']
})
export class Bai18Component implements OnInit {
  newEn = '';
  newVn = '';
  isshow = false;
  arrwords = [
    {id: 1, en: 'action', vn: 'hanh dong' , memorised: true, },
    {id: 2, en: 'actor', vn: 'dien vien' , memorised: false, },
    {id: 3, en: 'activity', vn: 'hoat dong' , memorised: true, },
    {id: 4, en: 'active', vn: 'chu dong' , memorised: true, },
    {id: 5, en: 'fight', vn: 'chien dau' , memorised: false, },

  ]

  constructor() { }

  ngOnInit() { }

  addContent()
  {
    this.arrwords.unshift({
      id: this.arrwords.length + 1,
      en: this.newEn,
      vn: this.newVn,
      memorised: false

    });
    this.newEn = '';
    this.newVn = '';
    this.isshow = false;
  }

  deleteContent(id:number)
  {
    const index = this.arrwords.findIndex(e => e.id == id);
    // ham xoa phan tu cua mang
    this.arrwords.splice(index, 1);
  }

  filterStatus = "xem-tat-ca";
  getShowStatus(memorised:boolean)
  {
    const dkxemtatca = this.filterStatus === "xem-tat-ca";
    const dkxemdanho = this.filterStatus ==='xem-da-nho' && memorised;
    const dkxemchuanho = this.filterStatus ==="xem-chua-nho" && !memorised;
    return dkxemtatca || dkxemdanho || dkxemchuanho;
  }

}



