import { Component } from '@angular/core';

@Component({
  selector: 'app-parents',
  templateUrl: './parents.component.html',
  styleUrls: ['./parents.component.css']
})
export class ParentsComponent {
  value = 0;
  changeValue(isadd:boolean)
  {
    if(isadd)
    {
      this.value = this.value + 1;
    }
    else
    {
      this.value = this.value - 1;
    }
    
  }

}



