import { Component, OnInit } from '@angular/core';
import { WeatherService } from "../weather.service";

@Component({
  selector: 'app-weatherhw',
  templateUrl: './weatherhw.component.html',
  styleUrls: ['./weatherhw.component.css'],
  providers:[WeatherService]
})
export class WeatherhwComponent  {
  txtname = '';
  cityname = ''; 
  temp = null;
  isLoading = false;
  
  constructor( private wtservice: WeatherService){}

  ngOnInit(): void{
    // this.wtservice.getWeather('Tokyo')
    // .then(temp => console.log(temp))
    // .catch(err => console.log(err));
    
  }

getValuetemp(){
  this.isLoading = true;
  this.wtservice.getWeather(this.txtname)
  .then(temp => {
        this.cityname = this.txtname;
        this.temp = temp;
        this.isLoading = false;
        this.txtname = "";
        
      })
  .catch(err =>{
        alert('cant find your city');
        this.isLoading = false;
        this.cityname = '';
        this.txtname ="";
        } )

}
getMessage()
{
  if(this.isLoading)
  {
    return "loading...";
  }
  
  return this.cityname === '' ? 'Please input your city name' : (this.cityname + ' is now ' + this.temp);
}



}



